
import Group from 'vux/src/components/group/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import { getAllBatch, addGoal, getAllUserProvince, getGoalCity, myInfo } from '@/api/index'
import { errorMsg } from '@/utils/common'
import BScroll from 'better-scroll'
export default {
components: { Group, Selector, CheckIcon, PopupPicker, Checker, CheckerItem },
data () {
return {
provList: [],
majorName: '',


province: [''],





checkLincense: false,
completeScroll: null,


}
},
props: {
show: Boolean,
code: String
},
computed: {
openid () {
return this.$store.state.user.openid
},
pageType () {
return this.$route.query.type || ''
}
},
mounted () {
this.init()

this.getMyGoal()
this.getList()





},
methods: {
gotoPage (name) {
if (name === -1) {
this.$router.go(-1)
} else {
this.$router.push({ name: name })
}
},
init () {
console.log('初始化：', this.$refs.completeContent)
this.$nextTick(() => {
this.completeScroll = new BScroll(this.$refs.completeContent, {
click: true
})
})
},
getMyGoal () {
getGoalCity({
openid: this.openid
}).then(resp => {
if (resp.data.code === 0) {
if (resp.data.data.city === '') {
this.getUserInfo()

} else {
this.province = [resp.data.data.city]
}
this.majorName = resp.data.data.majorname || '暂无'
}
})
},
changeProv (value) {





console.log(value[0])
},
toggleLincese (flag) {
this.$vux.confirm.show({
content: '使用提醒：在您使用高考预测系统前，请务必仔细阅读并透彻理解本声明。如果您使用本系统，您的行为将被视为对本声明全部内容的认可。若您使用本系统预测结果作为志愿填报，应当自行承担相应风险，我们不做任何形式的保证。中科智禾尊重并保护所有用户的个人隐私权，您个人资料，非经您亲自许可或根据相关法律、法规的强制性规定，不会主动地泄露给第三方。',
onConfirm: () => {
this.checkLincense = true
}
})
},
submit () {
const prov = this.province[0]
if (prov === '') {
errorMsg(this, '请选择省份')
return
}
addGoal({
openid: this.openid,
city: prov,
majorname: this.majorName
}).then(res => {

if (res.data.code === 0) {
this.$router.push({ name: 'my' })
} else {
errorMsg(this, res.data.msg)
}
})
},
getList () {
getAllUserProvince().then(res => {
this.provList = [res.data.data]
})
},
getUserInfo () {
myInfo(this.openid).then(res => {

if (res.data.code === 0) {
this.batch = [res.data.data.batch || '暂无']
this.category = [res.data.data.category || '暂无']
this.province = [res.data.data.province || '暂无']
}

})
}
}
}



